import React from "react";
import { Col, Row, Container } from "react-bootstrap";
import "./Skills.css";

function Skills(props) {
  return (
    <section className={"skills" + " " + props.className}>
      <Container className="skills-container">
        <Row xs={1} sm={1} md={2} lg={2} xl={2} className="skills-row">
          <SimpleGroupWithSubCategories
            category={props.displayText.leftGroup.category}
            subCategories={props.displayText.leftGroup.subCategories}
          />

          <CombinedGroup groups={props.displayText.rightGroup} />

          {props.displayText.others.map((properties) => {
            return (
              <SimpleGroup
                category={properties.category}
                items={properties.items}
              />
            );
          })}
        </Row>
      </Container>
    </section>
  );
}

function SimpleGroupWithSubCategories(props) {
  return (
    <Col className="skills-column">
      <section className="skills-groupset">
        <h1>{props.category}</h1>
        <ul>
          {props.subCategories.map((properties) => {
            return (
              <li>
                <div className="python-library">{properties.subCategory}</div>
                <ul>
                  {properties.items.map((items) => {
                    return <li>{items}</li>;
                  })}
                </ul>
              </li>
            );
          })}
        </ul>
      </section>
    </Col>
  );
}

function CombinedGroup(props) {
  return (
    <Col className="skills-column">
      <div className="inner-top">
        <section className="skills-groupset">
          <h1>{props.groups.top.category}</h1>
          <ul>
            {props.groups.top.items.map((items) => {
              return <li>{items}</li>;
            })}
          </ul>
        </section>
      </div>
      <div>
        <section className="skills-groupset">
          <h1>{props.groups.bottom.category}</h1>
          <ul>
            {props.groups.bottom.items.map((items) => {
              return <li>{items}</li>;
            })}
          </ul>
        </section>
      </div>
    </Col>
  );
}

function SimpleGroup(props) {
  return (
    <Col className="skills-column">
      <section className="skills-groupset">
        <h1>{props.category}</h1>
        <ul>
          {props.items.map((items) => {
            return <li>{items}</li>;
          })}
        </ul>
      </section>
    </Col>
  );
}

export default Skills;
