import React from "react";
import "./Projects.css";
import {
  Col,
  Row,
  Container,
  Card,
  ListGroup,
  ListGroupItem,
} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

function Projects(props) {
  return (
    <section className={"projects" + " " + props.className}>
      <Container className="cards-container">
        <Row xs={1} sm={1} md={2} lg={2} xl={3} className="cards-row">
          {props.displayText.map((attributes) => {
            return (
              <SimpleProject
                title={attributes.title}
                logos={attributes.logos}
                description={attributes.description}
                keywords={attributes.keywords}
                githubLink={attributes.githubLink}
                date={attributes.date}
              />
            );
          })}
        </Row>
      </Container>
    </section>
  );
}

function SimpleProject(props) {
  return (
    <Col className="cards-column">
      <Card bg="light" text="dark" className="mb-2 cards-item">
        <Card.Header className="cards-header">
          {props.title.map((titles) => {
            return <div>{titles}</div>;
          })}
        </Card.Header>
        <section className="cards-image-group">
          <Card.Img
            variant="top"
            src="images/emile-perron-xrVDYZRGdw4-unsplash.jpg"
            className="cards-background-image"
          />
          <div className="cards-brands-logos">
            {props.logos.map((logos) => {
              return <img src={logos} alt="brands-logos" />;
            })}
          </div>
        </section>
        <Card.Body className="cards-body">
          <Card.Text className="cards-text">{props.description}</Card.Text>
          <section className="cards-text-tail">
            <ListGroup className="list-group-flush">
              {props.keywords.map((keyword) => {
                return <ListGroupItem>{keyword}</ListGroupItem>;
              })}
            </ListGroup>
            <a href={props.githubLink}>
              <FontAwesomeIcon icon={faGithub} className="fa-icon" />
            </a>
          </section>
        </Card.Body>
        <Card.Footer className="cards-footer">
          <small className="text-muted">{props.date}</small>
        </Card.Footer>
      </Card>
    </Col>
  );
}

export default Projects;
