import React, { useState } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Header from "./components/Header";
import Projects from "./components/Projects";
import Awards from "./components/Awards";
import Skills from "./components/Skills";
import Footer from "./components/Footer";
import displayText from "./DisplayText";

function App() {
  const [page, setPage] = useState("projects");
  const [language, setLanguage] = useState("en");

  return (
    <div className="App">
      {console.log()}
      <Router>
        <Header
          className="App-header"
          page={page}
          setLanguage={setLanguage}
          language={language}
          displayText={displayText[language]["header"]}
        />
        <section>
          <Switch>
            <Route
              path="/"
              exact
              render={() => (
                <div>
                  {setPage("projects")}
                  <Projects
                    className="App-body"
                    displayText={displayText[language]["projects"]}
                  />
                </div>
              )}
            />
            <Route
              path="/awards"
              exact
              render={() => (
                <div>
                  {setPage("awards")}
                  <Awards
                    className="App-body"
                    displayText={displayText[language]["awards"]}
                  />
                </div>
              )}
            />
            <Route
              path="/skills"
              exact
              render={() => (
                <div>
                  {setPage("skills")}
                  <Skills
                    className="App-body"
                    displayText={displayText[language]["skills"]}
                  />
                </div>
              )}
            />
          </Switch>
        </section>
        <Footer
          className="App-footer"
          displayText={displayText[language]["footer"]}
        />
      </Router>
    </div>
  );
}

export default App;
