import React, { useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";

function Header(props) {
  const [loaded, setLoaded] = useState(false);

  return (
    <header className={"header" + " " + props.className}>
      <div className="language_tag">
        <a
          style={
            props.language.match("en")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("en");
          }}
        >
          EN
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("de")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("de");
          }}
        >
          DE
        </a>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a
          style={
            props.language.match("es")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
          onClick={() => {
            props.setLanguage("es");
          }}
        >
          ES
        </a>
      </div>
      <div>
        {!loaded ? (
          <img
            src="./images/dave-hoefler-wINEMQBBOGE-unsplash_low_res.jpg"
            alt="background_image_low_res"
            className="background_image"
          />
        ) : null}
      </div>
      <img
        className="background_image"
        src="./images/dave-hoefler-wINEMQBBOGE-unsplash.jpg"
        alt="background_image"
        style={!loaded ? { visibility: "hidden" } : {}}
        onLoad={() => setLoaded(true)}
      />
      <img
        src="./images/Lisandro_resbaja_web_Seeber_DSC4065-2.jpg"
        alt="personal_image"
        className="image"
      />

      <h1 className="presentation_text">
        <strong>{props.displayText.title}</strong>
        <br />
        {props.displayText.firstPartSubtitle}
        <br />
        {props.displayText.secondPartSubtitle}
        <br />
        {props.displayText.thirdPartSubtitle}
        <br />
      </h1>

      <ul className="links-list">
        <li
          style={
            props.page.match("projects")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/">
            {props.displayText.firstPage}
          </Link>
        </li>
        <li
          style={
            props.page.match("awards")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/awards">
            {props.displayText.secondPage}
          </Link>
        </li>
        <li
          style={
            props.page.match("skills")
              ? { textDecoration: "underline" }
              : { textDecoration: "none" }
          }
        >
          <Link exact="true" to="/skills">
            {props.displayText.thirdPage}
          </Link>
        </li>
      </ul>
    </header>
  );
}

export default Header;
